function dataURLtoBlob(dataurl) {
  let arr = dataurl.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], {type: mime});
}

/**
 *
 * @param x
 * @param y
 * @param width
 * @param height
 * @returns {{top: string, left: string}}
 */
function disPlayPositionCtrl(x, y, width, height) {
  let clientWidth = document.documentElement.clientWidth;
  let clientHeight = document.documentElement.clientHeight;
  let position = {};
  if (x < clientWidth - width && y < clientHeight - height) {
    // ()
    position = {
      left: x + 6 + "px",
      top: y + 6 + "px",
    };
  } else {
    // --()
    position = {
      left: x - 2 - width + "px",
      top: y - 6 - height + "px",
    };
  }
  return position;
}



// 
function compare(pro) {
  return function (obj1, obj2) {
    var val1 = obj1[pro];
    var val2 = obj2[pro];
    if (val1 > val2) { //
      return 1;
    } else if (val1 < val2) {
      return -1;
    } else {
      return 0;
    }
  }
}

// 
function getFloat(number, n) {
  n = n ? parseInt(n) : 0;
  if (n <= 0) return Math.round(number);
  number = Math.round(number * Math.pow(10, n)) / Math.pow(10, n);
  return number;
}

//
function toPoint(percent) {
  var str = percent.replace("%", "");
  str = str / 100;
  return str;
}

// 
function getRanNum() {
  var result = [];
  for (var i = 0; i < 4; i++) {
    var ranNum = Math.ceil(Math.random() * 25); //025
    //'A'ASCII65,A~ZASCII65 + 0~25;String.fromCharCode()ASCIIpush
    result.push(String.fromCharCode(65 + ranNum));
  }
  return result.join('');
}

export {
  dataURLtoBlob,
  disPlayPositionCtrl,
  getRanNum,
  getFloat,
  toPoint,
  compare
}
